.form-container{
    display: flex;
	align-items: center;
	justify-content: center;
    width: 100%;
	height: 100vh;
    position: fixed;
}
.form-container::before{
    content: "";
    width: 100%;
	height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url("nodado.jpg");
    position: absolute;
    opacity: 0.5;
    z-index: -1;
}
.login-form{ 
	display: flex;
	align-items: center;
	justify-content: center;
}
.text-center{
    width: 810px;
    margin-top: 25px;
	height: 460px;
    flex: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	border-top-right-radius: 50px;
	border-top-left-radius: 30px;
	border-bottom-left-radius: 30px;
    font-family: 'Times New Roman', Times, serif, Helvetica, sans-serif;
    font-size: 50px;
    background-color: #C7BCAB;
    color: #1F2E43;
	padding: 38px;
	margin-left: 19px;
	text-align: center;
}
.left {
	flex: 1;
	display: flex;
    width: 810px;
	align-items: center;
	justify-content: center;
	height: 460px;
	border-bottom-left-radius: 50px;
	border-top-right-radius: 30px;
	border-bottom-right-radius: 30px;
    background-color: #1F2E43;
	padding: 100px;
    font-family: 'Times New Roman', Times, serif, Helvetica, sans-serif;
}
.left1 {
	flex: 1;
	display: flex;
    width: 810px;
	align-items: center;
	justify-content: center;
	height: 460px;
	padding-right: 20px;
    background: linear-gradient( #1F2E43 50%,#C7BCAB 50%) ;
	border-radius: 33px;
}

.right {
	flex: 2;
}
.info-form {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.welcome {
	margin-bottom: 45px;
    color: #C7BCAB;
    font-size: 35px;
}
.input-field{
	width: 300px;
	height: 50px;
	margin: 5px 0;
	border-radius: 20px;
	padding: 5px;
	background-color: #C7BCAB;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: 150ms ease;
}
.input-field:hover{
	border: 3px solid #ebebeb;
	border-radius: 20px;
}
.input-field box-icon{
	flex: 1;
	background-color: #C7BCAB;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-left: 10px;
	padding-right: 13px;
}
box-icon{
	height: 20px;
}
.input-field input{
	flex: 10;
	background-color: #C7BCAB;
	outline: none;
	border: none;
	border-color: none;
	width: 100%;
	font-size: 13px;
	justify-content: center;
}
.input::placeholder{
	flex: 10;
	background: none;
	outline: none;
	border: none;
	width: 100%;
	font-size: 12px;
	justify-content: center;
	font-family: Arial, Helvetica, sans-serif, Helvetica, sans-serif, Helvetica, 
	sans-serif, Times, serif, Helvetica, sans-serif;
	color: #646464;
}
.account{
	color: #ebebeb;
	font-family: Arial, Helvetica, sans-serif, Helvetica, sans-serif, Helvetica, 
	sans-serif, Times, serif, Helvetica, sans-serif;
	justify-content: center;
	font-size: 13px;
}
.link{
	text-decoration:none;
	align-items: center;
	justify-content: center;
	color: #C7BCAB;
	padding: 5px;
	font-size: 13px;
	font-family: Arial, Helvetica, sans-serif, Helvetica, sans-serif, Helvetica, 
	sans-serif, Times, serif, Helvetica, sans-serif;
}
.link:hover{
	color: #e6e1db;
}
.btn1{
    border: none;
	outline: none;
	padding: 12px;
	background-color: #C7BCAB;
    color: #1F2E43;
	border-radius: 20px;
	width: 180px;
    font-family: Arial, Helvetica, sans-serif, Helvetica, sans-serif, Helvetica, 
	sans-serif, Times, serif, Helvetica, sans-serif;
	font-weight: bold;
	font-size: 12px;
	cursor: pointer;
    margin: 10px;
	margin-top: 15px;
}
