.form{
	align-items: center;
	justify-content: center;
    width: 100%;
	height: 100vh;
    position: fixed;
  
}
.form::before{
    content: "";
    width: 100%;
	height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url("nodado.jpg");
    position: absolute;
    opacity: 0.5;
    z-index: -1;
}
.main{
    padding-left: 100px;
    height: 100vh;
    padding-top: 10px;
	align-items: center;
	justify-content: center;
    width: 100%;
	height: 100vh;
    position: absolute;
}
.layout{
    display: flex;
}
.logo h6 {
    text-align: center;
    margin: 20px;
    padding: 20px;
    color: #C7BCAB; 
    font-size: 45px;
    font-family: 'Times New Roman', Times, serif, Helvetica, sans-serif;
}
.sidebar{
    height: 97vh;
    width: 300px;
    background-color: #1F2E43;
    border-top-left-radius: 30px;
	border-bottom-left-radius: 30px;
    color: #C7BCAB;
    
    
}
.menu-item{
    margin-top: 30px;
}
.menu-item a{
    color: #C7BCAB;
    text-decoration: none;
    font-size: 20px;
    
}
.menu-item i{
    font-size: 1.2rem;
    margin: 0 15px;
    padding-left: 10px;
    
}

.active {
   background-color: #C7BCAB;
   padding: 10px;
}

.active a, .active i{
    color: #1F2E43;
}
.content{
    width: 100%;
    height: 100vh;
}
.header{
    height: 10vh;
    width: 150vh;
    background-color: #1F2E43;
    border-top-right-radius: 30px;
    position: fixed;
    padding: 15px;
    font-family: 'Times New Roman', Times, serif, Helvetica, sans-serif;
}
.header-content i{
   padding-right: 5px;
    font-size: 1.2rem; 
    color: #C7BCAB;
}
.header-content i:hover{
     color: #e6e1db;
 }
.header-content .info{
    text-decoration: none;
    font-size: 20px;
    color: #C7BCAB;
    padding-left: 20px;
    margin: 0 10px;
    text-transform: uppercase;
    font-family: 'Times New Roman', Times, serif, Helvetica, sans-serif;
}
.header-content{
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}
.body{
    color: #1F2E43;
    background-color: #C7BCAB;
    margin-top: 96px;
    position: fixed;
    height: 87vh;
    width: 150vh;;
    text-align: center;
    padding: 25px;
    font-family: 'Times New Roman', Times, serif, Helvetica, sans-serif;
    border-bottom-right-radius: 30px;
}




